.sidebar {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    height: 100vh;
    padding-top: 55px;
    padding-bottom: 20px;
    width: 250px;
    background: #333333;
    text-align: center;

    @media (max-width: 1024px) {
        width: unset;
    }

    @media (max-width: 425px) {
        flex-direction: row;
        align-items: center;
        height: unset;
        padding: 0 20px;
    }
}

.sidebar--hidden {
    transform: translateX(-100%);
}

.sidebar__links-list {
    @media (max-width: 425px) {
        display: flex;
    }
}

.sidebar__profile {
    @media (max-width: 1024px) {
        padding: 40px 10px 0 10px;
    }

    @media (max-width: 425px) {
        padding: 0;
    }
}


.sidebar__img-wrapper {
    width: 150px;
    height: 150px;
    margin-left: 50px;
    border-radius: 50%;
    overflow: hidden;
    background: #eee;

    @media (max-width: 1024px) {
        margin: auto;
    }

    @media (max-width: 768px) {
        width: 75px;
        height: 75px;
    }
}


.sidebar__img {
    width: 100%;
    height: 100%;
    max-width: 100%;
    object-fit: cover;
}

.sidebar__name {
    margin-top: 20px;

    font-weight: normal;
    font-size: 24px;
    text-align: center;
    color: white;

    @media (max-width: 1024px) {
        margin-top: 15px;
        font-size: 20px;
    }

    @media (max-width: 768px) {
        display: none;
    }
}

.sidebar__link {
    display: block;
    padding: 18px 0;
    margin: 1px 1px 0 1px;

    background: #232323;
    color: #FFFFFF;

    font-size: 24px;
    text-decoration: none;

    p {
        margin-top: 5px;
    }

    @media (max-width: 1024px) {
        font-size: 18px;
        svg {
            width: 40px;
        }
    }

    @media (max-width: 768px) {
        p {
            display: none;
        }
    }

    @media (max-width: 425px) {
        padding: 18px 15px;
        margin-right: 2px;
    }
}

.sidebar__link--active {
    background: #22408D;
}

.sidebar__socials {
    @media (max-width: 425px) {
        display: none;
    }
}

.sidebar__social-list {
    display: flex;
    justify-content: center;

    li {
        margin-right: 28px;
        &:last-of-type {
            margin-right: 0;
        }
    }

    @media (max-width: 1024px) {
        li {
            margin-right: 15px;
        }
        img {
            width: 35px;
        }
    }

    @media (max-width: 768px) {
        flex-direction: column;

        li {
            margin-right: 0;
            margin-top: 10px;
        }

    }

    @media (max-width: 425px) {
        flex-direction: column;
    }
}