.home {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;

    background-image: url("big-background.jpg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

    text-align: center;

    @media (max-width: 1440px) {
        background-image: url("background-1.jpg");
    }

    @media (max-width: 992px) {
        padding: 0 10px;
    }
}

.home__title {
    color: white;
    font-weight: 500;
    font-size: 72px;

    margin: 0;

    @media (max-width: 1440px) {
        font-size: 62px;
    }

    @media (max-width: 992px) {
        font-size: 50px;
    }

    @media (max-width: 768px) {
        font-size: 35px;
    }
}

.home__subtitle {
    margin-top: 40px;
    font-size: 48px;
    color: white;

    @media (max-width: 1440px) {
        font-size: 36px;
    }

    @media (max-width: 992px) {
        margin-top: 20px;
        font-size: 24px;
    }

    @media (max-width: 768px) {
        margin-top: 10px;
        font-size: 18px;
    }
}